export default {
	name: 'image-loader',
	props: {
		originImage: {
			type: String
		},
		height:{
			type:Number
		},
		width:{
			type:Number
		},
		radius:{
			type:Number,
			default: 0
		}
	},
	data() {
		return {
			loadFlag: true
		};
	},
	computed:{
		uWidth(){
			return uni.upx2px(this.width) + 'px';
		},
		uHeight(){
			return uni.upx2px(this.height) + 'px';
		}
	},
	methods:{
		imageLoad(){
			this.loadFlag = false;
		}
	}
}
